<template>
  <div class="category">
    <!-- 分类 -->
    <van-nav-bar title="全部分类" fixed />
    <!-- 搜索框 -->
    <van-search @click="$router.push('/search')" readonly background="#f1f1f2" shape="round" placeholder="请输入搜索关键词" />
    <!-- 分类列表 -->
    <div class="list-box">
      <div class="left">
        <ul>
          <li v-for="(item, index) in list" :key="item.category_id" >
            <a href="javascript:;" @click="activeIndex = index" :class="{ active: activeIndex === index }">{{ item.name }}</a>
          </li>
        </ul>
      </div>
      <div class="right">
        <div class="cate-goods" @click="$router.push(`/searchlist?categoryId=${item.category_id}`)" v-for="item in list[activeIndex]?.children" :key="item.category_id">
          <img :src="item.image?.external_url">
          <p>{{ item.name}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getCategroupData } from '@/api/category'
export default {
  name: 'categoryIndex',
  data () {
    return {
      list: [], // 分类商品数据
      activeIndex: 0 // left导航下标
    }
  },
  methods: {
    // 获取分类页数据
    async getCategroupList () {
      const { data: { list } } = await getCategroupData()
      this.list = list
    }
  },
  created () {
    // 已进入页面渲染数据
    this.getCategroupList()
  }
}
</script>

<style lang="less" scoped>
.category {
  padding: 100px 0 50px;
  height: 100vh;
  // 分类
  .van-nav-bar {
    z-index: 1;
  }
  // 搜索
  .van-search {
    position: fixed;
    width: 100%;
    top: 46px;
    z-index: 1;
  }
  .list-box {
    height: 100%;
    display: flex;
    .left {
      width: 85px;
      height: 100%;
      background-color: #f3f3f3;
      overflow: auto;
      a {
        display: block;
        height: 45px;
        line-height: 45px;
        text-align: center;
        color: #444;
        font-size: 12px;
        &.active {
          color: #fb442f;
          background-color: #fff;
        }
      }
    }
    .right {
      flex: 1;
      height: 100%;
      background-color: #fff;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      align-content: flex-start;
      padding: 10px 0;
      overflow: auto;
      .cate-goods {
        width: 33.3%;
        margin-bottom: 10px;
        img {
          width: 70px;
          height: 70px;
          display: block;
          margin: 5px auto;
        }
        p {
          text-align: center;
          font-size: 12px;
        }
      }
    }
  }
}
</style>
